.p-cart {}

.cart-steps {
	display: flex;
	align-items: baseline;
	font-family: "geo-bold";
	font-size: 26px;
	margin-bottom: 50px;
	@include max-screen($mid-tablet) {
		flex-direction: column; }
	&__item {
		&:nth-child(1n+2) {
			@include icon(arrow, before);
			&:before {
				font-size: 14px;
				margin: 0 20px;
				@include max-screen($mid-tablet) {
					display: none; } } } }
	&__link {
		&--current {
			color: $color-main; } } }

