.p-about {}

.about-intro {
	@include bg-cover;
	background-image: url("../img/about-main.jpg");
	height: 580px;
	padding-left: 70px;
	padding-bottom: 90px;
	@include max-screen($big-tablet) {
		padding-left: 0;
		padding-bottom: 20px;
		height: 400px; }
	&__title {
		@include max-screen($big-tablet) {
			font-size: 36px; } }
	.pillow {
		@include max-screen($big-tablet) {
			max-width: 520px; } } }
