.pillow {
	display: inline-block;
	width: 100%;
	max-width: 600px;
	color: $color-white;
	background: $color-main;
	border: 5px solid $color-main;
	border-radius: 100px 100px 100px 0;
	padding: 80px 20px 60px 80px;
	z-index: 2;
	@include max-screen($notebook-width) {
		max-width: 680px;
		padding: 60px; }
	@include max-screen($smartphone-landscape) {
		padding: 40px 20px;
		border-radius: 60px; }
	//&__body
	//ax-width: 560px
	&__title {
		font-size: 64px;
		margin-bottom: 26px;
		@include max-screen($smartphone-landscape) {
			margin-bottom: 20px; } }
	&__text {
		font-size: 20px;
		@include max-screen($common-tablet) {
			font-size: 20px; }
		@include max-screen($smartphone-landscape) {
			font-size: 14px; } }
	&__btn {
		margin-top: 28px; }



	//&.is-red
	//order: 5px solid $color-main
	//ackground: $color-main
	&.is-white {
		background: transparent;
		border: 5px solid #ffffff; }

