/*
		|--------------------------------------------------------------------------
		| Make container
		|--------------------------------------------------------------------------
		| defaults vars in global.sass
	* *///
@mixin container($maw: $container-xl, $gutter: $container-gutter) {
	max-width: $maw;
	padding-left: $gutter;
	padding-right: $gutter;
	margin-left: auto;
	margin-right: auto; }

/*
		|--------------------------------------------------------------------------
		| Tea-grids
		|--------------------------------------------------------------------------
		| it applies to the parent
	* *///
@mixin make-grid($col-width, $col-gutter, $margin-top : null) {
	//width: 100%
	display: flex;
	flex-flow: row wrap;
	margin-left: -$col-gutter;
	> * {
		width: $col-width;
		padding-left: $col-gutter;
		float: left;
		@if ($margin-top != null) {
			margin-top: $margin-top; } } }

@mixin break-grid($col-width, $col-gutter, $margin-top: null) {
	margin-left: -$col-gutter;
	//@if $col-width == 50%
	//ustify-content: center
	> * {
		width: $col-width;
		padding-left: $col-gutter;
		@if ($margin-top != null) {
			margin-top: $margin-top; } } }

/*
		|--------------------------------------------------------------------------
		| Block shift (like a bootstrap)
		|--------------------------------------------------------------------------
		|
	* *///
$block-shift-cols: 12 !default;
@mixin block-shift($col-offset, $col-width, $col-shift-col: $col-shift-cols) {
	width: (100 / $col-shift-col) * $col-width + %;
	margin-left: (100 / $col-shift-col) * $col-offset + %;
	//@if ($col-shift-col != null)
	//width: 50px
	//@else
 }	//width: 150px
