.b-subscribe {
	background: $color-grey;
	padding: 100px 0;
	margin: 40px 0;
	@include max-screen($mid-tablet) {
		padding: 60px 0; }
	&__title {
		margin-bottom: 60px; }
	&__form {
		display: flex;
		justify-content: center;
		@include max-screen($small-tablet) {
			flex-direction: column; } }
	&__item {
		//flex-grow: 1
		height: 50px;
		@include max-screen($small-tablet) {
			margin-bottom: 15px; }
		&:nth-child(1n+2) {
			@include min-screen($small-tablet) {
				margin-left: 15px; } }
		&--name {
			@include min-screen($small-tablet) {
				flex-basis: 268px; } }
		&--email {
			@include min-screen($small-tablet) {
				flex-basis: 386px; } }
		&--submit {} } }
